.affairsWrapper {
    width: 300px;
    margin: 10px;
}
.buttons {
    margin: 5px;
    display: flex;
    justify-content: center;

}
.buttons .button {
    margin: 5px;
    width: 50px;
    height: 20px;
    border-radius: 3px;
    background-color: #05a9ca;
    cursor: pointer;
}

.buttons .active {
    background-color: #0bff5f;
}